"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Card } from "@/components/ui/card"
import { Label } from "@/components/ui/label"
import { toast } from "sonner"

export default function Home() {
  const [targetUrl, setTargetUrl] = useState("")
  const [alias, setAlias] = useState("")
  const [generatedLink, setGeneratedLink] = useState("")
  const [isLoading, setIsLoading] = useState(false)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()

    // Basic URL validation
    if (!targetUrl) {
      toast.error("Please enter a target URL")
      return
    }

    // Add http:// or https:// if not present
    let urlToValidate = targetUrl
    if (!urlToValidate.startsWith('http://') && !urlToValidate.startsWith('https://')) {
      urlToValidate = 'https://' + urlToValidate
    }

    // Validate URL format
    try {
      new URL(urlToValidate)
    } catch {
      toast.error("Please enter a valid URL")
      return
    }

    // Validate alias format if provided
    const trimmedAlias = alias.trim()
    if (trimmedAlias && !/^[a-z0-9-_]+$/.test(trimmedAlias)) {
      toast.error("Alias can only contain letters, numbers, hyphens, and underscores")
      return
    }

    // Start loading state after validation
    setIsLoading(true)

    try {
      const response = await fetch("/api/links", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ 
          targetUrl: urlToValidate,
          alias: trimmedAlias || undefined 
        }),
      })

      const data = await response.json()
      
      if (!response.ok) {
        throw new Error(data.error || "Failed to create tracking link")
      }

      setGeneratedLink(data.trackingLink)
      toast.success("Tracking link created successfully!")
    } catch (err) {
      toast.error(err instanceof Error ? err.message : "An error occurred. Please try again.")
    } finally {
      setIsLoading(false)
    }
  }

  const copyToClipboard = async () => {
    try {
      await navigator.clipboard.writeText(generatedLink)
      toast.success("Link copied to clipboard!")
    } catch (err) {
      toast.error("Failed to copy link")
    }
  }

  return (
    <div className="container mx-auto px-4 py-16">
      <Card className="max-w-2xl mx-auto p-8">
        <div className="space-y-6">
          <div className="text-center space-y-2">
            <h1 className="text-3xl font-bold">Link Tracker</h1>
            <p className="text-gray-500">
              Create tracking links to monitor visitor locations
            </p>
          </div>

          <form onSubmit={handleSubmit} className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="targetUrl">Target URL</Label>
              <Input
                id="targetUrl"
                type="text"
                placeholder="example.com"
                value={targetUrl}
                onChange={(e) => setTargetUrl(e.target.value)}
                className="font-mono"
                required
              />
              <p className="text-sm text-gray-500 mt-1">
                Enter a URL (with or without http/https)
              </p>
            </div>

            <div className="space-y-2">
              <Label htmlFor="alias">Custom Alias (optional)</Label>
              <Input
                id="alias"
                placeholder="my-custom-link"
                value={alias}
                onChange={(e) => setAlias(e.target.value.toLowerCase())}
                pattern="[a-z0-9-_]*"
                title="Only lowercase letters, numbers, hyphens, and underscores are allowed"
                className="font-mono"
              />
              <p className="text-sm text-gray-500 mt-1">
                Letters, numbers, hyphens, and underscores only
              </p>
            </div>

            <Button type="submit" className="w-full" disabled={isLoading}>
              {isLoading ? "Creating..." : "Create Tracking Link"}
            </Button>
          </form>

          {generatedLink && (
            <div className="space-y-2 pt-4">
              <Label>Your Tracking Link</Label>
              <div className="flex gap-2">
                <Input value={generatedLink} readOnly />
                <Button onClick={copyToClipboard} variant="outline">
                  Copy
                </Button>
              </div>
            </div>
          )}
        </div>
      </Card>
    </div>
  )
}
